import theme from '../theme'
import StandardInput from '../components/input/StandardInput'

const Index = () => (
  <>
    <div>
      <div>New style here</div>
      <StandardInput />
    </div>
    <style jsx global>{`
      body,
      input {
        font-size: ${theme.font.size.normal}px;
        font-family: ${theme.font.family.normal};
      }
    `}</style>
  </>
)

export default Index
